
  /*----------service list----------*/
  .img-service-list{
    height: 128px;
  }
  
  .img-service-card{
    border: 1px solid #00000029;
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 10px;
  }
  
  /*----------information-----------*/
  
  .img-detail{
    height: 282px;
  }
  
  .img-detail-sm{
    height: 81px;
  }
  
  @media (min-width: 480px){
    .card-sm{
      width: 72px;
      height: 72px;
      font-size: 14px;
    }
  }
  
  @media (min-width: 576px){
    .card-sm{
      width: 128px;
      height: 128px;
      font-size: 20px;
    }
  }
  
  .card-sm{
    justify-content: center;
    align-items: center;
    color: #888888;
  }
  
  .card-sm.active{
    border: 3px solid #D54100;
    color: #772400;
  }
  
  .card-sm.active span{
    color: #F0AD25;
  }
  
  .timeline::after{
    content: '';
    position: absolute;
    left: 15px;
    right: 0;
    width: calc(100% - 30px);
    height: 3px;
    background-color: #D54100;
  }
  
  .timeline-pointer{
    position: relative;
    display: flex;
    justify-content: center;
  }
  
  .timeline-pointer::before{
    content: '';
    position: absolute;
    top: -3px;
    width: 8px;
    height: 8px;
    background-color: #D54100;
    border-radius: 50%;
  }
  
  @media (min-width: 768px){
    .img-detail{
      height: 221px;
    }
    .img-detail-sm{
      height: 63px;
    }
  }
  
  @media (min-width: 992px){
    .position-lg-fixed{
      position: fixed;
    }
    .img-detail{
      height: 440px!important;
    }
    .img-detail-sm{
      height: 96px!important;
    }
  }