  .navbar-brand{
    background: url("https://ppt.cc/fNEjDx@.png") no-repeat center center;
    background-size: 100%;
    text-indent: 101%;
    overflow: hidden;
    width: 128px;
    height: 38px;
    white-space: nowrap;
    display: inline-block;
    margin: 0 auto;
  }

  .navbar{
    .absolute-top-center{
      left: 50%;
      background: #916019;
    }
    .collapse{
      &:not(.show){
        display: none;
      }
    }
    .nav-link{
      line-height: 1.2;
      &:hover{
        color: #916019 !important;
          border-bottom: 2px solid #772400;
      }
    }
    .navbar-toggler{
      border: 0px solid transparent;
    }
    .navbar-toggler-icon{
      background-image: url("https://ppt.cc/f7JXQx@.png");
    }
  }