ol, ul {
	list-style: none;
  padding: 0;
}

a{
  color: #772400;
  text-decoration: none!important;
  &:hover{
    color: #D5411A;
  }
}

.text-primary{
    color: #772400 !important;
}

.text-main{
    color: #F0AD25 !important;
  }
  
  .text-info{
    color: #D54100 !important;
  }

  .text-black{
    color: #212529 !important;
  }

  .btn-white{
    &:hover{
      background-color: #DEE2E6;
      border-radius: 10px!important;
    }
  }
  
  .btn-info{
    color: #fff;
    background-color: #D54100;
    border-color: #D54100;
    &:hover{
      background-color: #772400;
      border-color: #772400;
    }
  }

  .btn-outline-info{
    color: #D54100;
    border-color: #D54100;
    &:hover{
      color: #fff;
      background: #D54100;
      border-color: #D54100;
    }
  }

  .bg-main{
    background-color: #F0AD25 !important;
  }

  .page-link{
    color: #772400!important;
  }

img{
    max-width:100%;
    height: auto;
    object-fit: cover;
    object-position: center;
  }