/*----------banner----------*/

.rectangle{
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.4);
    transition: background-color .3s;
    
  }
  
  .bg-cover{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  
  .banner-content{
    height: auto;
  }
  
  @media (min-width: 992px){
    .banner-content{
    height: 736px;
  }
    .rounded-lg-0{
      border-radius: 0 !important;
    }
    .btn-down{
      bottom: 0!important;
    }
  }
  
  .btn-down{
    position: absolute;
    bottom: -102px;
    right: 0;
    width: 44px;
    height: 44px;
  }
  
  @media (min-width: 768px){
    .mt-md-25, .my-md-25{
      margin-top: 6.5rem !important;
    }
    .mb-md-6, .my-md-6{
      margin-bottom: 1.5rem !important;
    }
  }
  
  /*----------carousel----------*/
  
  .hotnanny{
    .swiper-container{
      width: 100%;
      height: 422px;
      margin-left: auto;
      margin-right: auto;
      position: relative;
      overflow: hidden;
      list-style: none;
      padding: 0;
      z-index: 1;
    }
    .swiper-wrapper{
      position: relative;
      width: 100%;
      height: 100%;
      z-index: 1;
      display: flex;
      transition-property: transform;
      box-sizing: content-box;
    }
    .swiper-slide{
      width: 343px!important;
      border: 1px solid #00000029;
      box-shadow: 0px 3px 6px #00000029;
      border-radius: 10px;
      margin-right: 13px;
      margin-left: 13px;
    }
    .object-cover{
      object-fit: cover;
      object-position: center center;
      padding: 5px;
    }
    .swipe-card{
      padding-top: 54px;
      padding-bottom: 28px;
    }
    .person{
      top: -50px;
    }
    .swiper-button-prev{
    display:none;
    }
    .swiper-button-next{
      padding:25px;
    border-radius:50%;
    background-color:#772400;
    }
    .swiper-button-next:after{
    color: #fff;
    font-size:10px;
    font-weight: bold;
    }
    .swiper-pagination-bullet{
      display: none;
    }
    .swipe-bg-size{
      width: 100%;
      height: 120px;
    }
  }
  
  @media (min-width: 991px){
    .swiper-slide{
      width: 240px!important;
    }
  }
  
  @media (min-width: 768px){
    .mr-md-5, .mx-md-5{
      margin-right: 1rem !important;
    }
  }
  
  /*----------classification----------*/
  .img-reservation{
      height: 132px;
    }
  .rectangle{
    &:hover{
      background-color: rgba(0, 0, 0, 0.6);
    }
  }
  
  @media (min-width: 992px){
    .img-reservation{
      height: 350px!important;
    }
  }
  
  @media (min-width: 768px){
    .img-reservation{
      height: 210px;
    }
  }
  
  /*----------statistics----------*/
  .bg-statistics{
    background-image: url(https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/master/petpet-week8/index-9.jpeg);
    .number-lg{
      font-size: 48px;
    }
    .statistic-list{
      height: 212px;
      font-size: 20px;
      border-width: 2px !important;
    }
  }