/*----------banner----------*/
.banner-service{
    height: 352px;
  }
  
  .bg-cover{
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }
  
  @media (min-width: 768px){
    .banner-service{
      height: 315px;
    }
  }

/*----------carousel----------*/

.hotnanny{
    .swiper-container{
      width: 100%;
      height: 422px;
      margin-left: auto;
      margin-right: auto;
      position: relative;
      overflow: hidden;
      list-style: none;
      padding: 0;
      z-index: 1;
    }
    .swiper-wrapper{
      position: relative;
      width: 100%;
      height: 100%;
      z-index: 1;
      display: flex;
      transition-property: transform;
      box-sizing: content-box;
    }
    .swiper-slide{
      width: 343px!important;
      border: 1px solid #00000029;
      box-shadow: 0px 3px 6px #00000029;
      border-radius: 10px;
      margin-right: 13px;
      margin-left: 13px;
    }
    .object-cover{
      object-fit: cover;
      object-position: center center;
      padding: 5px;
    }
    .swipe-card{
      padding-top: 54px;
      padding-bottom: 28px;
    }
    .person{
      top: -50px;
    }
    .swiper-button-prev{
    display:none;
    }
    .swiper-button-next{
      padding:25px;
    border-radius:50%;
    background-color:#772400;
    }
    .swiper-button-next:after{
    color: #fff;
    font-size:10px;
    font-weight: bold;
    }
    .swiper-pagination-bullet{
      display: none;
    }
    .swipe-bg-size{
      width: 100%;
      height: 120px;
    }
  }
  
  @media (min-width: 991px){
    .swiper-slide{
      width: 240px!important;
    }
  }
  
  @media (min-width: 768px){
    .mr-md-5, .mx-md-5{
      margin-right: 1rem !important;
    }
  }
  
  /*----------service list----------*/
  .img-service-list{
    height: 128px;
  }
  
  .img-service-card{
    border: 1px solid #00000029;
    box-shadow: 0px 3px 6px #00000029;
    border-radius: 10px;
  }